import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, ToastController, AlertCmp, AlertController } from 'ionic-angular';
import { UserProvider } from '../../providers/user/user';
import { Clipboard } from '@ionic-native/clipboard';
import { TranslatePipe, TranslateService, LangChangeEvent } from '@ngx-translate/core';
@IonicPage({
  name: 'page-verify-2FA',
  segment: 'verify-2FA'
})

@Component({
  selector: 'page-verify-2FA',
  templateUrl: 'verify-2FA.html'
})

export class verify2FAPage {

  secret: any;

  code: any;
  img: any;
  constructor(public navCtrl: NavController, public translate: TranslateService, public clipboard: Clipboard, public navparams: NavParams, public forgotCtrl: AlertController, public user: UserProvider, public loadingCtrl: LoadingController, public toastCtrl: ToastController) {
    this.translate.setDefaultLang('en');
    this.translate.use('es');
    // this.userinfo();



  }

  ngOnInit() {

    this.secret = this.navparams.get('secret');
    this.img = this.navparams.get('image');
    console.log(this.secret);
    console.log(this.img);

  }
  // copy clipboard
  copy() {

    this.clipboard.copy(this.secret);
    this.clipboard.paste().then(
      (resolve: string) => {
        alert(resolve);
      },
      (reject: string) => {
        alert('Error: ' + reject);
      }
    );
    console.log('sssss');
  }
  // verify 2FA enable
  verify() {
    

    this.user.verify2fa(this.code).subscribe(
      data => {

        console.log(data);

        let datas = JSON.parse(JSON.stringify(data));
        let data2 = JSON.parse(datas._body);

        console.log(data2.success);
        if (data2.success) {
          this.translate.get('enable-2FA-page-2.complete_success_toast').subscribe((translated: string) => {
            // console.log(translated);
            // //=> 'Hello world'

            // You can call instant() here
            var toast1 = this.translate.instant('enable-2FA-page-2.complete_success_toast');

            let toast = this.toastCtrl.create({
              message: toast1,
              duration: 3000,
              position: 'top'
            });

            toast.onDidDismiss(() => {
              console.log('Dismissed toast');
              localStorage.setItem("2fa", JSON.stringify(1));
              this.navCtrl.setRoot('page-my-security');

            });

            toast.present();

          });

        } else {

          this.translate.get('enable-2FA-page-2.complete_error_toast').subscribe((translated: string) => {
            // console.log(translated);
            // //=> 'Hello world'

            // You can call instant() here
            var complete_error_toast = this.translate.instant('enable-2FA-page-2.complete_error_toast');

            let toast = this.toastCtrl.create({
              message: complete_error_toast,
              duration: 3000,
              position: 'top'
            });

            toast.onDidDismiss(() => {
              console.log('Dismissed toast');

            });

            toast.present();

          })

        }



      })


  }


}



